@import '../../assets/styles/functions';
@import '../../assets/styles/colors';

.MainView {
  margin         : 0 auto;
  padding        : db(800) 10vw 100px;
  display        : flex;
  justify-content: center;
  position       : relative;
  background     : url('../../assets/images/banana-minion.png') no-repeat 50% 50px;
  background-size: 37%;

  &__temp-image {
    width     : 40vw;
    height    : 40vw;
    object-fit: cover;

    @media screen and (max-width: 600px) {
      width : 90vw;
      height: 90vw;
    }
  }

  &__title-wrapper {
    display        : flex;
    justify-content: center;
  }

  &__title {
    margin        : 0;
    font-size     : 35px;
    color         : $textColor;
    letter-spacing: 2px;
  }

  &__title-icon {
    margin-right   : 10px;
    width          : 40px;
    height         : 40px;
    background     : url('../../assets/images/bugle3.png') no-repeat 50% 50%;
    background-size: cover;

    &:last-of-type {
      margin-left: 7px;
      transform  : scale(-1, 1);
    }
  }

  &__players-list {
    width   : fit-content;
    height  : fit-content;
    position: relative;
    z-index : 10;

    .MainView__title {
      margin-bottom: 25px;
    }
  }

  &__banana {
    width          : db(300);
    height         : db(360);
    background     : url('../../assets/images/one-banana.png') no-repeat 50% 50%;
    background-size: cover;
    position       : absolute;
    filter         : drop-shadow(0 0 15px $primaryBlue);

    &_one {
      top      : db(-30);
      left     : db(-80);
      transform: rotate(200deg);
    }

    &_two {
      top      : db(350);
      left     : db(-30);
      transform: rotate(225deg);
    }

    &_three {
      top      : db(240);
      left     : db(-60);
      transform: rotate(190deg);
    }

    &_four {
      top      : db(-10);
      left     : db(-170);
      transform: rotate(60deg);
    }

    &_five {
      top      : db(460);
      left     : db(-40);
      transform: rotate(45deg);
    }

    &_six {
      top      : db(350);
      left     : db(-150);
      transform: scale(-1, 1) rotate(50deg);
    }

    &_seven {
      top      : db(690);
      left     : db(-90);
      transform: rotate(225deg);
    }
  }

  &__bananas-left {
    position: fixed;
    top     : 60px;
    left    : 0;
  }

  &__bananas-right {
    position : fixed;
    top      : 60px;
    right    : 0;
    transform: scale(-1, 1);
  }

  @media screen and (max-width: 1366px) {
    &__title {
      font-size: 30px;
    }
  }

  @media screen and (max-width: 1024px) {
    padding        : ds(500) 10vw 100px;
    background-size: 45%;
  }

  @media screen and (max-width: 768px) {
    padding        : t(500) 10vw 100px;
    background-size: 60%;

    &__bananas-left {
      display: none;
    }

    &__bananas-right {
      display: none;
    }
  }

  @media screen and (max-width: 600px) {
    padding            : mb(580) 10vw 50px;
    background-position: 50% 0;
    background-size    : 100%;

    &__title {
      font-size: 25px;
    }

    &__title-icon {
      width          : 35px;
      height         : 35px;  
    }
  }
}